import { Component, OnInit } from '@angular/core';
import { LikesService } from 'src/app/services/likes.service'
import { LoginService } from 'src/app/services/login.service'
import { InstalacionesService} from 'src/app/services/instalaciones.service'
import { Auth } from 'aws-amplify'
@Component({
  selector: 'app-area-personal',
  templateUrl: './area-personal.component.html',
  styleUrls: ['./area-personal.component.scss']
})
export class AreaPersonalComponent implements OnInit {
  likes: any = [];
  instalaciones: any = [];
  userInstalaciones: any = [];
  userLikes: any;
  userName: String = "";
  constructor(private likeServices: LikesService,
    public loginService: LoginService,
    private instalacionesService: InstalacionesService ) { }

  ngOnInit(): void {
    this.likeServices.getLikes().subscribe(
      res => {
        this.likes = res;
      },
      err => console.log(err)
    );
      this.getUsername();



    this.likes.forEach( (like: { username: string }) => {
      if(like.username == this.userName)
        this.userLikes.push(like);
    });


    this.instalacionesService.getInstalaciones().subscribe(
      res => {
        this.instalaciones = res;
      },
      err => console.log(err)
    );

    this.instalaciones.forEach((instalacion: any) => {
      this.userLikes.forEach((like: any) => {
        if(like.cod_instalacion == instalacion.cod_instalacion){
          this.userInstalaciones.push(instalacion);
        }
      });

    });

  }

  getUsername(){
    Auth.currentAuthenticatedUser()
        .then(user => {

          this.userName = user.userName
          
        })
      }

  muestra(){
    console.log(this.likes);
    console.log(this.userLikes);
    console.log(this.userInstalaciones);
    console.log(this.instalaciones);
  }






}

