import { Component, OnInit, EventEmitter, Input } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'
import { Instalacion } from 'src/app/models/Instalacion';
import { InstalacionesService } from 'src/app/services/instalaciones.service';
import { ComunidadService } from 'src/app/services/comunidad.service';
import { Horario } from 'src/app/models/horario';
import { HorarioService } from 'src/app/services/horario.service';
import { NotifierService } from 'angular-notifier';
import {FormsModule} from "@angular/forms";
import { ReservaService } from 'src/app/services/reserva.service';
import { Reserva } from 'src/app/models/reserva';
import { Comunidad } from 'src/app/models/Comunidad';
import { LoginService } from 'src/app/services/login.service'
import { LikesService } from 'src/app/services/likes.service'
import { Like } from 'src/app/models/Like'
import { OwlOptions } from 'ngx-owl-carousel-o';
import { Auth } from 'aws-amplify'
@Component({
  selector: 'app-detalle-instalacion',
  templateUrl: './detalle-instalacion.component.html',
  styleUrls: ['./detalle-instalacion.component.scss']
})




export class DetalleInstalacionComponent implements OnInit {

  reserva!: Reserva;

  likes: any = [];

  userName: any;
  instalacion: Instalacion = {
    cod_instalacion: 0,
    cod_comunidad: 0,
    hora_apertura: '',
    hora_cierre: '',
    aforo: 0,
    descripcion: '',
    tipo_instalacion: '',
    nombre: '',
    precio: 0,
    persona_ajeno: false,
    limpieza: 0,
    comunicacion: 0,
    ubicacion: 0,
    calidad: 0,
    img: '',
    img2: '',
    img3: '',
    reserva_maxima: 0

  };
    customOptions: OwlOptions = {
    loop: true,
    mouseDrag: false,
    touchDrag: false,
    pullDrag: false,
    dots: false,
    autoplay:true,
    autoHeight: false,
    autoplayTimeout:5000,
    navText: ['', ''],
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 1
      },
      740: {
        items: 1
      }
    },
    nav: true
  }

  selected: string = "";

  pases: number = 1;

  guarda: boolean = false;

  horario: any = [];

  comunidad: Comunidad = {
    cod_comunidad: 0,
    nombre: "",
    ubicacion: "",
    mapa: ""
  };

  like: Like = {
    id: 0,
    cod_instalacion: 0,
    username: "",
    created_at: new Date()
  }

  fecha!: any;
  muestra: boolean = false;
  hoy: Date = new Date();

  private readonly notifier: NotifierService;

  constructor(private instalacionesService: InstalacionesService,
    private horarioService:HorarioService,
    private comunidadService:ComunidadService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    notifierService: NotifierService,
    private serviceReserva: ReservaService,
    private loginService: LoginService,
    private likeService: LikesService) {
    this.notifier = notifierService;
  }


  ngOnInit(): void {
    this.getUsername;
    const params = this.activatedRoute.snapshot.params;
    if(params.cod_instalacion){
      this.instalacionesService.getInstalacion(params.cod_instalacion)
      .subscribe(
        res => {
          console.log(res);
          this.instalacion = res;
          //this.edit = true;
        },
          err => console.error(err)
          );


          this.horarioService.getHorario(params.cod_instalacion).subscribe(
            res => {
              this.horario = res;
              console.log(res);
            },
            err => console.log(err)
          );

          this.comunidadService.getComunidad(params.cod_instalacion).subscribe(
            res => {
              this.comunidad = res;
              console.log(res);
            },
            err => console.log(err)
          );

          this.likeService.getLikes().subscribe(
            res => {
              this.likes = res;
            },
            err => console.log(err)
          );

          this.likes.forEach((like: { username: string | undefined; cod_instalacion: number | undefined; }) => {
            if(like.username == this.loginService.userData.username && like.cod_instalacion == this.instalacion.cod_instalacion)
               this.guarda = true;
          });

          console.log(this.guarda);
          console.log(this.likes);

    }




    this.reserva = {
      turno: "",
      cod_instalacion: 0,
      cod_reserva: 0,
      usuario: "",
      total: 0,
      num_pases: 0,
      fecha: new Date(0)

    }



    /*const chatbotElement = document.querySelector('amplify-chatbot');
    chatbotElement.addEventListener('chatCompleted', handleChatComplete);
*/


  }

  /*selectOption(id: number) {
    //getted from event
    console.log(id);
    //getted from binding
    console.log(this.selected)
  }*/


  ngOnDestroy(): void {
    /*const chatbotElement = document.querySelector('amplify-chatbot');
    chatbotElement.removeEventListener('chatCompleted', handleChatComplete);
  */}
  getUsername(){
    Auth.currentAuthenticatedUser()
        .then(user => {

          this.userName = user.username;
        })
      }


  guardarReserva(){
    this.reserva = {
      turno: this.selected.toString(),
      cod_instalacion: this.instalacion.cod_instalacion,
      cod_reserva: 0,
      usuario: this.loginService.userData.username,
      total: 0,
      num_pases: this.pases,
      fecha: this.fecha
    }
    console.log(this.reserva);

    this.serviceReserva.setReserva(this.reserva);

    if(this.fecha!=null && this.selected!= null && this.pases != null )

      this.router.navigate(['confirmacion', this.instalacion.cod_instalacion]);
      else{
        this.notifier.show({
          type: 'error',
          message: 'Compruba que has rellenado todos los campos!',
          id: 'THAT_NOTIFICATION_ID', // Again, this is optional
        });
      }
  }





  guardaLike(){
    this.guarda = true;
    this.like.username = this.loginService.userData.username;
    this.like.cod_instalacion = this.instalacion.cod_instalacion;
    delete this.like.created_at;
    this.likeService.saveLike(this.like)
    .subscribe(
      res => {
        console.log(res);


      },

      err => console.error(err)
    );
  }

  borraLike(){
    this.guarda = false;
    let mg : Like = {};
    console.log(this.likes);

      this.likes.forEach((like: { username: string | undefined; cod_instalacion: number | undefined; }) => {
        if(like.username == this.userName && like.cod_instalacion == this.instalacion.cod_instalacion)
           mg = like;
      });


    this.likeService.deleteLike(mg.id).subscribe(
      res => {
        console.log(res);
      },

      err => console.error(err)
    );
  }







}








