import { Component, OnInit } from '@angular/core';
import { InstalacionesService } from 'src/app/services/instalaciones.service'
import { ReservaService } from 'src/app/services/reserva.service'
import { Auth } from 'aws-amplify'
import { Instalacion } from 'src/app/models/Instalacion';
@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.scss']
})
export class AdminComponent implements OnInit {

  instalaciones: any;
  imagen: "" | undefined;
  reservas: any;
  reserva: Boolean =false;
  instalacion: any;
  reser: any;
  constructor(private instalacionService: InstalacionesService,
    private reservaService: ReservaService,) { }

  ngOnInit(): void {
    this.getInstalaciones();
    this.getReservas();
    this.añadirCampos();

  }

  getInstalaciones(){
    this.instalacionService.getInstalaciones().subscribe(
      res => {
        this.instalaciones = res;
        console.log(Auth.currentUserInfo);
      },
      err => console.log(err)
    );
  }

  getReservas(){
    this.reservaService.getReservas().subscribe(
      res => {
        this.reservas = res;
      },
      err => console.log(err)
    );
  }

  añadirCampos(){
    this.reservas.forEach((reserva: { [x: string]: any; cod_instalacion: number; }) => {
      var img = this.obtenerImagen(reserva.cod_instalacion);
      var nombre = this.obtenerTitulo(reserva.cod_instalacion);
      var newValue = "nombre";
      var newValue2 = "img";
      this.reservas.reserva[newValue] = img;
      this.reservas.reserva[newValue2] = nombre;
    });
  }

  deleteInstalacion(id: string){
    this.instalacionService.deleteInstalacion(id).subscribe(
      res => {
        console.log(res);
        this.getInstalaciones();
      },

      err => console.error(err)
    );
  }

  obtenerImagen(cod_instalacion: number){
    let instalacion: any;
    this.instalacionService.getInstalacion(cod_instalacion).subscribe(
    res => {

      return instalacion.img;
    },
    err => console.log(err)

  );

  }

  obtenerTitulo(cod_instalacion: number){
    let instalacion: any;
    this.instalacionService.getInstalacion(cod_instalacion).subscribe(
    res => {
      instalacion = res;
      return instalacion.nombre;
    },
    err => console.log(err)

  );
  }
}



